$text-color-primary: #334750;
$text-color-primary-dark: #0e191d;
$color-pr-orange: #f07924;

body {
  font-family: "Montserrat", sans-serif;
}

.head-centre {
  font-size: 2rem;
  text-align: center;
  margin: 0 0 2em;
}

// Nav Menu
.logo {
  width: 300px;
  margin-left: 10px;
}
.navbar-nav {
  margin-right: 5rem;
}
.navbar {
  background-color: #ffff;
}
.transparent {
  background: none !important;
}

.floatingNav {
  border-radius: 2px;
  box-shadow: 0px 1px 10px #999;
}
.menu {
  position: absolute;
  right: 0;
  button {
    margin-right: 50px;
  }
  background: #fff;
  z-index: 500;
}
.nav-item {
  margin-right: 20px;
  a {
    text-decoration: none;
    color: $text-color-primary;
    &:hover {
      color: $text-color-primary-dark;
    }
  }
}
.navbar-toggler-icon {
  background-image: url(/static/images/menu-icon.svg) !important;
}
//End Nav

// Footer
footer {
  background-color: #fbfbfb;
}
.branding {
  .logo-footer {
    overflow: hidden;
  }
  .address {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .social-links {
    display: flex;
    justify-content: space-between;
    a {
      color: $text-color-primary;
      &:hover {
        color: $text-color-primary-dark;
      }
    }
  }
}

.quick-links {
  padding: 1rem;
}
.news-letter {
  text-align: center;
  padding: 1rem;
  form {
    margin-top: 2rem;
  }
  input {

    background: #f1f1f1;
    color: $text-color-primary;
    font: inherit;
    border: 0;
    outline: 0;
    padding: 18px 18px;
  }
  button {
    background: linear-gradient(90deg, #f29d25 0%, #ef7824 61.74%);
    border: 0;
    outline: 0;
    color: #ffff;
    font-weight: bold;
    padding: 18px 30px;
    margin-left: 20px;
    &:hover {
      transition: transform 0.25s ease;
      transform: scale(1.02);
    }
  }
  .copyright {
    margin-top: 40px;
    line-height: 2;
    font-size: 0.8em;
    font-weight: 500;
    a {
      text-decoration: none;
    }
  }
}

.footer-menu {
  list-style: none;
  padding-left: 0;
  li {
    padding-top: 5px;
    padding-bottom: 5px;
    a {
      color: $text-color-primary;
      text-decoration: none;
      &:hover {
        color: $text-color-primary-dark;
      }
    }
  }
}
//End Footer

$color-pr-orange: #f07924;
.head-title {
  margin-bottom: 20px;
  font-size: 50px;
  font-family: "Montserrat", sans-serif;
  color: black;
  font-weight: 500;
  span {
    color: $color-pr-orange;
    font-weight: 600;
  }
}

.form-group {
  position: relative;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  display: inline-block;
  width: 100%;
  input {
    height: 1.9rem;
  }
  textarea {
    resize: none;
  }
  .control-label {
    margin-top: -1.9rem;
    text-align: center;
    align-content: center;
    position: relative;
    display: block;
    pointer-events: none;
    padding-left: 0.125rem;
    z-index: 1;
    color: #a7a7a7;
    font-size: 1rem;
    font-weight: normal;
    -webkit-transition: all 0.28s ease;
    transition: all 0.28s ease;
  }
}
.form-group .bar {
  position: relative;
  border-bottom: 0.12rem solid #727272;
  display: block;
}
.form-group .bar::before {
  content: "";
  height: 0.125rem;
  width: 0;
  left: 50%;
  bottom: -0.0625rem;
  position: absolute;
  background: $color-pr-orange;
  -webkit-transition: left 0.28s ease, width 0.28s ease;
  transition: left 0.28s ease, width 0.28s ease;
  z-index: 2;
}
.form-group input,
.form-group textarea {
  display: block;
  background: none;
  text-align: center;
  padding: 0.125rem 0.125rem 0.0625rem;
  font-size: 1rem;
  border-width: 0;
  border-color: transparent;
  line-height: 1.9;
  width: 100%;
  color: transparent;
  -webkit-transition: all 0.28s ease;
  transition: all 0.28s ease;
  box-shadow: none;
}
.form-group input:focus,
.form-group input:valid,
.form-group input.form-file,
.form-group input.has-value,
.form-group textarea:focus,
.form-group textarea:valid,
.form-group textarea.form-file,
.form-group textarea.has-value {
  color: #333;
}
.form-group input:focus ~ .control-label,
.form-group input:valid ~ .control-label,
.form-group input.form-file ~ .control-label,
.form-group input.has-value ~ .control-label,
.form-group textarea:focus ~ .control-label,
.form-group textarea:valid ~ .control-label,
.form-group textarea.form-file ~ .control-label,
.form-group textarea.has-value ~ .control-label {
  font-size: 0.8rem;
  color: gray;
  top: -1rem;
  left: 0;
}
.form-group input:focus,
.form-group textarea:focus {
  outline: none;
}
.form-group input:focus ~ .control-label,
.form-group textarea:focus ~ .control-label {
  color: $color-pr-orange;
}
.form-group input:focus ~ .bar::before,
.form-group textarea:focus ~ .bar::before {
  width: 100%;
  left: 0;
}
.has-error .legend.legend,
.has-error.form-group .control-label.control-label {
  color: #d9534f;
}
.has-error.form-group .form-help,
.has-error.form-group .helper,
.has-error.checkbox .form-help,
.has-error.checkbox .helper,
.has-error.radio .form-help,
.has-error.radio .helper,
.has-error.form-radio .form-help,
.has-error.form-radio .helper {
  color: #d9534f;
}
.has-error .bar::before {
  background: #d9534f;
  left: 0;
  width: 100%;
}
.button {
  position: relative;
  background: white;
  border: 1px solid white;
  display: block;
  font-size: 1.1rem;
  border: solid #ff9347;
  margin: 0 auto;
  padding: 0.75rem 3rem;
  cursor: pointer;
  -webkit-transition: background-color 0.28s ease, color 0.28s ease,
    box-shadow 0.28s ease;
  transition: background-color 0.28s ease, color 0.28s ease,
    box-shadow 0.28s ease;
  overflow: hidden;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
.button span {
  color: black;
  position: relative;
  z-index: 1;
}
.button::before {
  content: "";
  position: absolute;
  background: #ff6a00;
  border: 50vh solid #ff9347;
  width: 30vh;
  height: 30vh;
  border-radius: 50%;
  display: block;
  top: 50%;
  left: 50%;
  z-index: 0;
  opacity: 1;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
}
.button:hover {
  color: $color-pr-orange;
  transition: transform 0.25s ease;
  transform: scale(1.02);
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12),
    0 3px 5px -1px rgba(0, 0, 0, 0.2);
}
.button:active::before,
.button:focus::before {
  -webkit-transition: opacity 0.28s ease 0.364s, -webkit-transform 1.12s ease;
  transition: opacity 0.28s ease 0.364s, -webkit-transform 1.12s ease;
  transition: transform 1.12s ease, opacity 0.28s ease 0.364s;
  transition: transform 1.12s ease, opacity 0.28s ease 0.364s,
    -webkit-transform 1.12s ease;
  -webkit-transform: translate(-50%, -50%) scale(1);
  transform: translate(-50%, -50%) scale(1);
  opacity: 0;
}
.button:focus {
  outline: none;
}
.error {
  color: #ed4337;
  padding: 0;
  text-align: center;
}
.success {
  padding: 0;
  text-align: center;
  color: #f07924;
  font-weight: bold;
}

