/* For mobile phones: */
@media only screen and (max-width: 768px) {
  .logo {
    width: 200px;
  }
  .branding {
    margin-top: 20px;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
    p {
      text-align: center !important;
    }
    .social-links {
      margin-left: auto;
      margin-right: auto;
    }
    .logo-footer {
      margin-right: auto;
      margin-left: auto;
      width: 75%;
      img {
        width: 100% !important;
      }
    }
  }
  .head-title {
    font-size: 2.5rem;
  }
  .content {
    margin-left: 30px;
    margin-right: 30px;
  }
  .news-letter {
    input {
      width: 100%;
    }
    button {
      margin-left: 0;
      margin-top: 10px;
    }
  }
  .menu {
    button {
      margin-right: 10px;
    }
  }
}
/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) {
  .news-letter {
    margin-left: 50px;
  }
}
@media only screen and (max-width: 900px) {
  .navbar-nav {
    height: 100vh;
    width: 100vw;
  }
  .show {
    display: block;
  }

  .navbar-collapse {
    top: 0px;
    position: fixed;
    width: 100vw;
    left: 0;
    height: 100vh;
    background-color: #ffff;
    .navbar-nav {
      top: 2%;
      text-align: center;
      position: absolute;
      .nav-item {
        padding: 3% 10%;
        a {
          font-size: 30px;
          letter-spacing: 4px;
          line-height: 45px;
          font-weight: bold;
        }
      }
    }
  }
}
/* Only for desktops*/
@media only screen and (min-width: 768px) {
  .branding {
    padding-top: 1rem;
  }
  .content {
    margin-left: 200px;
    margin-right: 200px;
  }

}
/* Only for large desktops more than 720p*/

@media screen and (min-width: 992px) {
  .branding {
    padding-left: 4rem;
    padding-right: 4rem;
  }
  .content {
    margin-left: 180px;
    margin-right: 180px;
  }
  .show {
    display: contents;
  }
  .collapse:not(.show){
    display: none !important;
  }
  .navbar-nav {
    flex-direction: row;
  }
  .menu{
    display: block;
  }
}
@media (max-width: 768px) {
  .btnPrev,
  .btnNext {
    background-size: 20px;
  }
  .bose-img{
    img{
      border-radius: 40px;
      width: 50% !important;
    }
  }
}
@media (max-width: 385px) {
  .btnPrev,
  .btnNext {
    display: none;
  }
  .col-10 {
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }

}

